/* @after Core/widget/CalendarPanel.css */

:root, :host {
    /**
     * Min-width of the entire picker
     */
    --b-date-picker-min-width                   : 25em;

    /**
     * Font weight for the title
     */
    --b-date-picker-title-font-weight           : 600;

    /**
     * Font size for the day letters
     */
    --b-date-picker-day-font-size               : 1em;

    /**
     * Padding for the date elements
     */
    --b-date-picker-date-padding                : 0.5em;

    /**
     * Font size for the date elements
     */
    --b-date-picker-date-font-size              : 1em;

    /**
     * Font weight for today's date
     */
    --b-date-picker-today-font-weight           : inherit;

    /**
     * Font weight for the selected date
     * @state Selected
     */
    --b-date-picker-selected-font-weight        : 600;

    /**
     * Border-radius for the selected week
     * @state Selected
     */
    --b-date-picker-selected-week-border-radius : 2px;

    /**
     * Block margin for the date cells
     */
    --b-date-picker-cell-margin-block           : 2px; /* Gives some gap between highlighted week and date cell */

    /**
     * Font-size for the top toolbar (that holds the navigation buttons + month/year)
     */
    --b-date-picker-toolbar-font-size           : 1em;

    /**
     * Day letter color
     */
    --b-date-picker-day-color                   : var(--b-neutral-30);

    /**
     * Date color
     */
    --b-date-picker-date-color                  : var(--b-neutral-30);

    /**
     * Today's date's background
     */
    --b-date-picker-today-background            : transparent;

    /**
     * Selected date's text color
     * @state Selected
     */
    --b-date-picker-selected-color              : var(--b-primary-30);

    /**
     * Date picker's header padding, when configured to be shown
     */
    --b-date-picker-header-padding              : 1.5em 0;

    /**
     * Color
     */
    --b-date-picker-color                       : var(--b-primary);
}

