/* @after Core/widget/Checkbox.css */

:root, :host {
    /**
     * Size of the radio button "checkmark" (the dot)
     */
    --b-radio-check-gap            : 0.2em;

    /**
     * Radio button background
     */
    --b-radio-background           : var(--b-neutral-100);

    /**
     * Background of the radio button when checked
     * @state Checked
     */
    --b-radio-checked-background   : var(--b-radio-background);

    /**
     * Radio button's border-color when checked
     * @state Checked
     */
    --b-radio-checked-border-color : var(--b-radio-checked-color);
}

