.b-tool {
    display          : grid;
    appearance       : none;
    background-color : transparent;
    border           : none;
    border-radius    : 50%;
    cursor           : pointer;
    padding          : 0;
    width            : 1.25em;
    place-items      : center;
    transition       : color .2s;
    aspect-ratio     : 1 / 1;

    color            : var(--b-panel-header-color);

    &:focus-visible {
        outline : var(--b-widget-focus-outline-width) solid var(--b-widget-focus-outline-color);
    }
}
