    }

    .b-color-orange {
        --b-primary : var(--b-color-orange);
    }

    .b-color-deep-orange {
        --b-primary : var(--b-color-deep-orange);
    }

    .b-color-brown {
        --b-primary : var(--b-color-brown);
    }

    .b-color-lighter-gray {
        --b-primary : var(--b-color-lighter-gray);
    }

    .b-color-light-gray {
        --b-primary : var(--b-color-light-gray);
    }

    .b-color-gray {
        --b-primary : var(--b-color-gray);
    }

    .b-color-black {
        --b-primary : var(--b-color-black);
    }
}

/* endregion */

/* region Animations */

@keyframes b-anim-fade-in {
    0% {
        opacity : 0;
    }
    100% {
        opacity : 1;
    }
}

@keyframes b-anim-fade-out {
    0% {
        opacity : 1;
    }
    100% {
        opacity : 0;
    }
}

@keyframes b-anim-slide-in-from-right {
    30% {
        translate : -50%;
        opacity   : 0.5;
    }
    30.01% {
        translate : 50%;
    }
    100% {
        translate : 0;
        opacity   : 1
    }
}

@keyframes b-anim-slide-in-from-left {
    30% {
        translate : 50%;
        opacity   : 0.5;
    }
    30.01% {
        translate : -50%;
    }
    100% {
        translate : 0;
        opacity   : 1;
    }
}

@keyframes b-anim-slide-in-from-below {
    30% {
        translate : 0 -50%;
        opacity   : 0.5;
    }
    30.01% {
        translate : 0 50%;
    }
    100% {
        translate : 0;
        opacity   : 1;
    }
}

@keyframes b-anim-slide-in-from-above {
    30% {
        translate : 0 50%;
        opacity   : 0.5;
    }
    30.01% {
        translate : 0 -50%;
    }
    100% {
        translate : 0;
        opacity   : 1;
    }
}

.b-slide-in-next,
.b-slide-in-previous {
    animation : b-anim-slide-in-from-left 0.3s ease 0s 1 normal
}

/* Vertical slide in is always next come from below and previous from above */
.b-slide-vertical {
    &.b-slide-in-next {
        animation-name : b-anim-slide-in-from-below;
    }

    &.b-slide-in-previous {
        animation-name : b-anim-slide-in-from-above;
    }
}

/* Horizontal side in respects RTL. */
/* In RTL, next comes from the left and previous from the right. */
.b-slide-in-next:not(.b-slide-vertical) {
    animation-name : b-anim-slide-in-from-right;
}

.b-slide-in-previous:not(.b-slide-vertical) {
    animation-name : b-anim-slide-in-from-left;
}

.b-rtl {
    &.b-slide-in-next:not(.b-slide-vertical) {
        animation-name : b-anim-slide-in-from-left;
    }

    &.b-slide-in-previous:not(.b-slide-vertical) {
        animation-name : b-anim-slide-in-from-right;
    }
}

/* Class added to parent element of elements being slid around by the above classes. */
/* While the animation is in flight, no overflow must appear in the parent element. */
.b-sliding-child-element {
    overflow : clip;
}

.b-transition-expand-collapse {
    &.b-aligned-below {
        animation : b-anim-expand-downwards 0.3s forwards;
    }
    &.b-aligned-above {
        animation : b-anim-expand-upwards 0.3s forwards;
    }
    &.b-hiding {
        &.b-aligned-below {
            animation : b-anim-collapse-upwards 0.3s forwards;
        }
        &.b-aligned-above {
            animation : b-anim-collapse-downwards 0.3s forwards;
        }
    }
}

/*
 * Animations which expands and collapse vertically.
 * The 5px extra is to allow for box-shadow to be visible.
 */
@keyframes b-anim-expand-downwards {
    0% {
        clip-path : rect(-20px calc(100% + 20px) 0 -20px);
        opacity   : 0;
    }
    100% {
        clip-path : rect(-20px calc(100% + 20px) calc(100% + 20px) -20px);
        opacity   : 1;
    }
}

@keyframes b-anim-collapse-upwards {
    0% {
        clip-path : rect(-5px calc(100% + 5px) calc(100% + 5px) -5px);
        opacity   : 1;
    }
    100% {
        clip-path : rect(-5px calc(100% + 5px) 0 -5px);
        opacity   : 0;
    }
}

@keyframes b-anim-expand-upwards {
    0% {
        clip-path : rect(100% calc(100% + 5px) 100% -5px);
        opacity   : 0;
    }
    100% {
        clip-path : rect(-5px calc(100% + 5px) calc(100% + 5px) -5px);
        opacity   : 1;
    }
}

@keyframes b-anim-collapse-downwards {
    0% {
        clip-path : rect(-5px calc(100% + 5px) calc(100% + 5px) -5px);
        opacity   : 1;
    }
    100% {
        clip-path : rect(100% calc(100% + 5px) 100% -5px);
        opacity   : 0;
    }
}

/* endregion */
