:root, :host {
    /**
     * Splitter size (width or height depending on orientation)
     */
    --b-splitter-size               : 4px; /* .25em leads to half pixel fails in a lot of tests */

    /**
     * Size when hovered
     * @state Hovered
     */
    --b-splitter-hover-size         : 8px;

    /**
     * Size when hovered on touch devices
     * @state Hovered
     */
    --b-splitter-touch-hover-size   : 16px;

    /**
     * Size when disabled
     * @state Disabled
     */
    --b-splitter-disabled-size      : 1px;

    /**
     * Splitter color
     */
    --b-splitter-color              : var(--b-border-6);

    /**
     * Splitter color when hovered
     * @state Hovered
     */
    --b-splitter-hover-color        : var(--b-border-7);

    /**
     * Splitter color when disabled
     * @state Disabled
     */
    --b-splitter-disabled-color     : var(--b-border-7);

    /**
     * Splitter buttons size
     */
    --b-splitter-button-size        : 1em;

    /**
     * Splitter buttons size on touch platforms
     */
    --b-splitter-touch-button-size  : 1.3em;

    /**
     * Splitter buttons icon color
     */
    --b-splitter-button-icon-color  : var(--b-border-4);

    /**
     * Splitter buttons icon color when hovered
     * @state Hovered
     */
    --b-splitter-button-hover-color : var(--b-border-2);

    /* Internal vars that should not be set by themes */
    --bi-splitter-overflow          : visible; /* Allow any touch handle to overflow */
    --bi-splitter-inner-top         : 0;
    --bi-splitter-inner-left        : 0;
    --bi-splitter-inner-translate   : 0 0;
}

.b-splitter {
    position                 : relative;
    touch-action             : none;

    background               : var(--b-splitter-color);
    flex                     : 0 0 var(--b-splitter-size);
    overflow                 : var(--bi-splitter-overflow);

    --bi-splitter-inner-size : var(--b-splitter-size);

    &.b-disabled {
        pointer-events         : none;

        --b-splitter-size      : var(--b-splitter-disabled-size);
        --b-splitter-color     : var(--b-splitter-disabled-color);
        --bi-splitter-overflow : clip;
    }

    &.b-hover,
    &.b-moving,
    &.b-show-buttons {
        /* Display splitter buttons on hover */
        .b-splitter-buttons {
            display : flex;
        }
    }

    /* Define the hover / move inner */
    &::after {
        content    : '';
        position   : absolute;
        z-index    : 10000; /* Above most things */
        transition : all .2s;

        background : var(--b-splitter-color);

        top        : var(--bi-splitter-inner-top);
        left       : var(--bi-splitter-inner-left);
        width      : var(--bi-splitter-inner-width);
        height     : var(--bi-splitter-inner-height);
        translate  : var(--bi-splitter-inner-translate);
    }

    &.b-horizontal {
        min-height : var(--b-splitter-size);

        &::after {
            --bi-splitter-inner-height : var(--bi-splitter-inner-size);
            --bi-splitter-inner-width  : 100%;
        }

        &:not(.b-drag-disabled) {
            cursor : ns-resize;

            /* When hovered or being moved, the inner pops out to cover a larger area. */
            &:hover,
            &.b-hover,
            &.b-moving {
                --b-splitter-color : var(--b-splitter-hover-color);

                &::after {
                    --bi-splitter-inner-top       : 50%;
                    --bi-splitter-inner-translate : 0 -50%;
                    --bi-splitter-inner-size      : var(--b-splitter-hover-size);
                }
            }
        }

        /* Rotate buttons for horizontal splitter */
        .b-splitter-buttons {
            rotate : 90deg;
        }
    }

    &.b-vertical {
        min-width : var(--b-splitter-size);

        &::after {
            --bi-splitter-inner-height : 100%;
            --bi-splitter-inner-width  : var(--bi-splitter-inner-size);
        }

        &:not(.b-drag-disabled) {
            cursor : ew-resize;

            /* When hovered or being moved, the inner pops out to cover a larger area. */
            &:hover,
            &.b-hover,
            &.b-moving {
                --b-splitter-color : var(--b-splitter-hover-color);

                &::after {
                    --bi-splitter-inner-left      : 50%;
                    --bi-splitter-inner-translate : -50% 0;
                    --bi-splitter-inner-size      : var(--b-splitter-hover-size);
                }
            }
        }
    }

    /* Hide splitter if not having two siblings */
    &:last-child,
    &:first-child {
        visibility : hidden;
        flex       : 0;
        min-width  : 0;
    }

    /* When hovered or being moved, the inner pops out to cover a larger area. */
    /* How much it does that by depends on whether the pointer capability is coarse or fine. */
    @media (pointer : coarse) {
        --b-splitter-hover-size : var(--b-splitter-touch-hover-size);
    }

    /* region RTL */
    &.b-rtl {
        .b-splitter-buttons {
            translate : 50% -50%;
        }

        &.b-vertical .b-splitter-buttons {
            scale : -1 1;
        }

        .b-splitter-button-expand {
            justify-content : flex-end;
            translate       : 100%;

        }

        .b-splitter-button-collapse {
            justify-content : flex-start;
            translate       : -100%;
        }
    }
    /* endregion */
}

/* The div surrounding the buttons */
.b-splitter-buttons {
    display            : none;
    position           : absolute;
    height             : 2.4em;
    width              : 2.4em;
    z-index            : 10001;
    inset-inline-start : 50%;
    translate          : -50% -50%;
    transition         : top 0.2s ease-in-out, inset-inline-start 0.2s ease-in-out;

    font-size          : var(--b-splitter-button-size);

    &:has(.b-disabled) {
        pointer-events : none;
    }
}

.b-splitter-button-collapse,
.b-splitter-button-expand {
    flex        : 1;
    cursor      : pointer;
    display     : flex;
    align-items : center;
    position    : relative;
    padding-top : .1em;

    background  : var(--b-splitter-color);

    &:hover .b-splitter-button-icon {
        color : var(--b-splitter-button-hover-color);
    }

    &.b-disabled {
        cursor         : default;
        visibility     : hidden !important;
        pointer-events : none;
    }

    &:not(.b-disabled) {
        pointer-events : auto;
    }
}

.b-splitter-button-collapse {
    border-top-left-radius    : 100% 50%;
    border-bottom-left-radius : 100% 50%;
    justify-content           : flex-end;
}

.b-splitter-button-expand {
    border-top-right-radius    : 100% 50%;
    border-bottom-right-radius : 100% 50%;
}

.b-splitter-button-icon {
    cursor        : pointer;
    height        : 1.5em;
    font-size     : 1.3em;
    font-weight   : 400;
    align-content : center;

    color         : var(--b-splitter-button-icon-color);
}

/* region Touch */

.b-splitter-button-touch-area {
    height   : 3em;
    top      : -.3em;
    width    : 2em;
    position : absolute;
}

.b-splitter-button-collapse .b-splitter-button-touch-area {
    inset-inline-start : -.9em;
}

.b-splitter-button-expand .b-splitter-button-touch-area {
    inset-inline-end : -.9em;
}

/* endregion */

