/* @after Core/widget/Panel.css */

:root, :host {
    /**
     * Gap between the years in the year picker, and months in the month picker.
     */
    --b-year-picker-gap               : 0.5em;

    /**
     * Padding inside the year picker and the month picker.
     */
    --b-year-picker-padding           : var(--b-year-picker-gap);

    /**
     * Font weight for the title of the year picker and the month picker
     */
    --b-year-picker-title-font-weight : 600;

    /**
     * Font weight for the years in the year picker and the months in the month picker
     */
    --b-year-picker-year-font-weight  : var(--b-widget-font-weight);

    /**
     * Color for the years in the year picker and the months in the month picker
     */
    --b-year-picker-year-color        : var(--b-neutral-30);
}

