/* @after Core/widget/Panel.css */

:root, :host {
    /**
     * Fieldset border-radius
     */
    --b-field-set-border-radius   : var(--b-widget-border-radius);

    /**
     * Fieldset border-width
     */
    --b-field-set-border-width    : 1px;

    /**
     * Fieldset padding
     */
    --b-field-set-padding         : var(--b-panel-padding);

    /**
    * Fieldset border-color
    */
    --b-field-set-border-color    : var(--b-border-3);

    /**
     * Fieldset background
     */
    --b-field-set-background      : var(--b-neutral-100);

    /**
     * Fieldset label font size
     */
    --b-field-set-label-font-size : 0.8em;
}

.b-field-set {
    position    : relative;
    overflow    : visible;
    /* When it has a label, it gets b-field added. Prevent some of that cls rules */
    align-items : stretch;
    gap         : 0;

    color       : var(--b-primary);

    &:not(.b-radio-group) {
        display : flex;
    }

    fieldset {
        background    : var(--b-field-set-background);
        border        : var(--b-field-set-border-width) solid var(--b-field-set-border-color);
        border-radius : var(--b-field-set-border-radius);
        padding       : var(--b-field-set-padding);

        /* width         : 90%; */
        /* max-width     : 90%; */
        margin        : 0;
    }

    &.b-columns fieldset {
        grid-template-columns : repeat(var(--bi-container-columns), auto);
    }

    &:not(:has(.b-inline), .b-label-align-before) > .b-label {
        position           : absolute;
        inset-block-start  : 0;
        inset-inline-start : 0;
        translate          : calc(1em * var(--b-rtl-negate)) -50%;
        padding-inline     : 0.5em;
        z-index            : 1;
        line-height        : 1em;

        font-size          : var(--b-field-set-label-font-size);

        &::before {
            content    : '';
            position   : absolute;
            inset      : 0;
            z-index    : -1;
            clip-path  : inset(calc(.5em - var(--b-field-set-border-width)) 0 0 0);
            background : var(--b-field-set-background);
        }
    }

    &.b-label-align-before:not(:has(.b-inline)) > .b-label {
        align-self : start;
    }

    /* Match toolbar color when used directly in a toolbar */
    /* .b-toolbar-content > & { */
    /*     --b-field-set-background : var(--b-toolbar-background); */
    /* } */
}

legend.b-field-set-legend {
    position : absolute;
    top      : -10000px;
    clip     : rect(0, 0, 0, 0);
}

.b-toolbar-content > .b-field-set {
    --b-field-set-background : var(--b-toolbar-background, var(--b-neutral-100));
}
