.b-internal {
    --bi-value : null;
}

/*region Chat bubbles*/

.b-chat-bubbles {
    display    : flex;
    flex-flow  : column nowrap;
    gap        : 0.5em;
    overflow-x : clip;
    overflow-y : auto;

    /* Creates an initial gap*/
    &:before {
        content : "";
    }

    .b-message {
        display        : flex;
        flex-direction : column;
        gap            : 0.2em;
        overflow       : clip;
        width          : 100%;
        flex-shrink    : 0;

        align-self     : var(--b-chat-panel-message-align-self);
    }

    .b-bubble {
        padding       : 0.5em 0.75em;
        opacity       : 1;
        transition    : opacity .2s, translate .2s;
        translate     : 0;
        z-index       : 1;
        min-height    : 2.5em;
        line-height   : 1.5em;
        max-width     : 80%;
        word-wrap     : break-word;
        overflow      : auto;

        border-radius : var(--b-chat-panel-border-radius);
        align-self    : var(--b-chat-panel-message-align-self);
        background    : var(--b-chat-panel-message-background);
        color         : var(--b-chat-panel-message-color);

        .b-aborted {
            opacity : .3;
        }

        .b-failed {
            color : var(--b-color-red);
        }

        strong {
            font-weight : 600;
        }
    }

    .b-undone .b-bubble {
        opacity : .3;
    }

    .b-async .b-bubble {
        width       : 3.5em;
        display     : flex;
        align-items : center;
        overflow    : clip;
    }

    .b-async .b-asyncstatus {
        font-size            : .8em;
        opacity              : .8;
        padding-inline-start : .2em;
    }

    .b-bubble-loading {
        position           : relative;
        inset-inline-start : -2em;
        width              : .5em;
        height             : .5em;
        border-radius      : 50%;
        animation          : b-anim-jumping-dots 2s infinite;
        box-shadow         : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }

    .b-bubble-tools {
        .b-undone-tag {
            border-radius  : 1em;
            font-size      : .75em;
            padding-inline : .5em;
            background     : var(--b-color-red);
            color          : var(--b-neutral-100);
        }
        display          : flex;
        padding-inline   : 0.75em;
        gap              : .5em;
        align-items      : center;
        transition       : opacity .2s, translate .2s;
        transition-delay : .2s;
        justify-content  : var(--b-chat-panel-message-them-align-self);
    }

    .b-bubble-tool {
        font-size : var(--b-chat-panel-font-size);
        color     : var(--b-chat-panel-tool-color);

        &:hover {
            --b-chat-panel-tool-color : var(--b-chat-panel-tool-hover-color);
        }
    }

    button.b-bubble-tool {
        cursor         : pointer;
        border         : none;
        background     : transparent;
        padding-inline : 0;
    }

    .b-timestamp {
        font-size : .7em;
        color     : var(--b-chat-panel-tool-color);
    }

    .b-us {
        --b-chat-panel-message-align-self : var(--b-chat-panel-message-us-align-self);
        --b-chat-panel-message-background : var(--b-chat-panel-message-us-background, var(--b-primary));
        --b-chat-panel-message-color      : var(--b-chat-panel-message-us-color);
    }

    .b-them {
        --b-chat-panel-message-align-self : var(--b-chat-panel-message-them-align-self);
        --b-chat-panel-message-background : var(--b-chat-panel-message-them-background);
        --b-chat-panel-message-color      : var(--b-chat-panel-message-them-color);
    }

    .b-confidence {
        position      : relative;
        width         : 1em;
        height        : .6em;
        border-radius : .25em;
        border        : 1px solid var(--b-chat-panel-tool-color);
        overflow      : clip;

        &::before {
            display    : block;
            position   : absolute;
            content    : '';
            height     : 100%;
            opacity    : 0.6;
            width      : calc(var(--bi-value) * 100%);
            background : var(--b-chat-panel-tool-color);
        }
    }

    .b-options {
        display         : flex;
        flex-wrap       : wrap;
        justify-content : flex-end;
        gap             : .5em;
        opacity         : 1;
        transition      : height .2s, opacity .2s, gap .2s;
        height          : auto;
        overflow        : hidden;
        align-items     : var(--b-chat-panel-message-us-align-self);

        &:has(.b-selected) {
            gap : 0;

            .b-option {
                pointer-events : none;
            }

            .b-option:not(.b-selected) {
                opacity          : 0;
                height           : 0;
                min-height       : 0;
                padding-block    : 0;
                display          : none;
                transition-delay : 0s;
            }
        }
    }

    .b-option {
        background          : none;
        font-size           : 1em;
        padding             : 0.5em 0.75em;
        opacity             : 1;
        transition          : opacity .2s, translate .2s, height .2s, min-height .2s, padding .2s, display .2s;
        transition-delay    : .2s;
        translate           : 0;
        min-height          : 2.5em;
        line-height         : 1.5em;
        overflow            : hidden;
        transition-behavior : allow-discrete;
        interpolate-size    : allow-keywords;
        border              : 1px solid var(--b-chat-panel-tool-color);
        border-radius       : var(--b-chat-panel-border-radius);

        &:not(.b-outdated):hover {
            cursor     : pointer;
            background : var(--b-chat-panel-option-hover-background);
        }

        &.b-selected {
            border     : none;
            background : var(--b-chat-panel-message-us-background, var(--b-primary));
            color      : var(--b-chat-panel-message-us-color);
        }

        /* Slide options in with a slight delay between each*/
        &:nth-child(2) {
            transition-delay : .3s;
        }

        &:nth-child(3) {
            transition-delay : .4s;
        }

        &:nth-child(4) {
            transition-delay : .5s;
        }

        &:nth-child(5) {
            transition-delay : .6s;
        }

        &:nth-child(6) {
            transition-delay : .7s;
        }

        /* Below nth-child rules to enforce 0 delay*/
        &.b-outdated {
            opacity          : .3;
            transition-delay : 0s;
        }
    }

    .b-bottom-anchor {
        min-height : 1px;
    }
}

@starting-style {
    .b-chat-bubbles.b-animate {
        .b-bubble {
            opacity   : 0;
            translate : 0 1em
        }

        .b-bubble-tools {
            opacity   : 0;
            translate : 0 -1em
        }

        .b-option {
            translate : -100% 0;
            opacity   : 0;
        }
    }
}

