@keyframes b-anim-jumping-dots {
    0% {
        box-shadow : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }
    16.667% {
        box-shadow : 2em -.5em 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }
    33.333% {
        box-shadow : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }
    50% {
        box-shadow : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em -.5em 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }
    66.667% {
        box-shadow : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }
    83.333% {
        box-shadow : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em -.5em 0 0 var(--b-chat-panel-loading-color);
    }
    100% {
        box-shadow : 2em 0 0 0 var(--b-chat-panel-loading-color), 2.75em 0 0 0 var(--b-chat-panel-loading-color), 3.5em 0 0 0 var(--b-chat-panel-loading-color);
    }
}

/*endregion*/

/*region Chat panel*/

.b-chat-panel-mixin.b-panel {
    > header {
        gap : .5em;

        &:after {
            display : none;
        }

        .b-tool {
            font-size : 1.2em;
        }
    }

    .b-chat-panel-mixin-content {
        --b-panel-with-header-padding : 0;

        & > div {
            padding-inline : 1em;
        }
    }

    .b-chat-bubbles {
        padding-top : 1em;
        flex        : 1;
    }

    .b-intro {
        display        : flex;
        flex-direction : column;
        align-items    : center;
        gap            : .5em;
        text-align     : center;
        margin-bottom  : 1em;
        color          : var(--b-chat-panel-intro-color);

        div {
            line-height : 1.5em;
        }

        .b-chat-avatar {
            border-radius : 50%;
            margin-bottom : 1em;
            background    : var(--b-primary); /* Not using the commented variable since this is a direct use */
            width         : var(--b-chat-panel-avatar-size);
        }
    }

    .b-example-prompt {
        font-size  : .9em;
        font-style : italic;
        cursor     : pointer;
        color      : var(--b-chat-panel-example-prompt-color);

        &::before {
            content : '"';
        }

        &::after {
            content : '"';
        }

        &:hover {
            color : var(--b-chat-panel-example-prompt-hover-color);
        }
    }

    .b-text-field {
        padding-bottom : 1em;

        .b-field-inner {
            border-radius : var(--b-chat-panel-border-radius);
        }

        .b-field-trigger {
            color : var(--b-chat-panel-tool-color);

            &:before {
                font-size : 1.5em;
            }

            &:hover {
                color : var(--b-chat-panel-tool-hover-color);
            }

            &.b-send-chat-msg {
                transition : color .2s;
            }

            /* Tab focus style*/
            .b-using-keyboard &.b-icon-mic:focus {
                padding-inline-end : 0;
                margin-inline-end  : .6em;
                outline-offset     : .4em;
            }
        }

        & .b-send-chat-msg:not(.b-disabled) {
            color : var(--b-chat-panel-tool-color-active);
        }
    }
}

/*endregion*/

/*region Chat button*/

.b-chat-button.b-button {
    position         : absolute;
    bottom           : 1em;
    inset-inline-end : 1em;
    border           : none;
    border-radius    : 50%;
    font-size        : 1em;
    width            : 2.75em;
    height           : 2.75em;
    display          : grid;
    place-items      : center;
    padding          : 0;
    margin           : 0;
    box-shadow       : 0 2px 8px 0 rgba(99, 99, 99, 0.2);
    scale            : 1;
    transition       : scale .2s;
    min-height       : 0;
    z-index          : 10000;
    outline          : none;
    color            : var(--b-chat-button-color, #FFF);
    background       : var(--b-chat-button-background, var(--b-primary));

    .b-button-menu-icon {
        display : none;
    }

    i {
        position    : relative;
        display     : grid;
        place-items : center;
        font-size   : 1.3em;
    }

    &:hover {
        scale : 1.1;
    }

    &:active {
        scale : 1;
    }

    .b-using-keyboard &:focus:not(:active) {
        box-shadow : 0 0 0.8em #3183fe;
    }

    &.b-chat-button-animating i::before {
        animation : b-anim-chat-button-icon-transition .2s;
    }

    &.b-chat-closed {
        --content : "\f27a";
    }

    &.b-chat-closed-previous {
        --previous-content : "\f27a";
    }

    &.b-chat-open {
        --content : "\f00d";
    }

    &.b-chat-open-previous {
        --previous-content : "\f00d";
    }

    &.b-voice-speaking {
        --content : "\e473";

        & i {
            animation : b-anim-voice-speaking .7s infinite;
        }
    }

    &.b-voice-speaking-previous {
        --previous-content : "\e473";
    }

    &.b-voice-waiting {
        --content : "\f110";
        animation : b-anim-rotate 2s infinite linear;
    }

    &.b-voice-waiting-previous {
        --previous-content : "\f110";
    }

    &.b-voice-active {
        --content : "\f130";

        & i {
            animation : b-anim-voice-active 0.9s ease-out infinite;
        }
    }

    &.b-voice-active-previous {
        --previous-content : "\f130";
    }

    &.b-voice-inactive {
        --content  : "\f130";
        background : #AAAAAA;
    }

    &.b-voice-inactive-previous {
        --previous-content : "\f130";
    }

    .b-button-icon::before {
        content : var(--content);
    }
}

@keyframes b-anim-voice-speaking {
    0% {
        transform : none;
        opacity   : 1;
    }
    49.9% {
        transform : none;
    }
    50% {
        transform : scaleX(-1);
        opacity   : .5;
    }
    100% {
        transform : scaleX(-1);
        opacity   : 1;
    }
}

@keyframes b-anim-voice-active {
    0% {
        transform : scale(1);
        opacity   : 1;
    }
    25% {
        transform : scale(0.95);
    }
    60% {
        transform : scale(1.05);
        opacity   : .8;
    }
    100% {
        transform : scale(1);
        opacity   : 1;
    }
}

@keyframes b-anim-chat-button-icon-transition {
    0% {
        font-size : 1em;
        content   : var(--previous-content);
    }
    50% {
        font-size : 0.1em;
    }
    100% {
        font-size : 1em;
        content   : var(--content);
    }
}

@starting-style {
    .b-chat-button.b-button {
        scale : 0;

        i:after {
            opacity : 0;
        }
    }
}

/*endregion*/

/*#region AI chat panel*/

.b-is-speaking button.b-bubble-tool[data-ref="readAloud"] {
    color : blue;
}

.b-aichat-panel-mixin.b-panel .b-text-field .b-is-recording {
    color : blue;

    &:hover {
        color : blue;
    }
}

.b-bubble {
    ul, ol {
        padding-inline-start : 1em;
    }

    > p:first-child {
        margin-block-start : 0;
    }

    > p:last-child {
        margin-block-end : 0;
    }

}

.b-aichat-panel-mixin .b-panel-header .b-tool[data-ref="offline"] {
    color : #f99d9d;
}

/* #end region*/
