/* @after Core/widget/TextField.css */

/* TODO Clean up */
.b-time-field {
    bry-time {
        align-items                      : center;

        --b-time-field-button-background : color-mix(in srgb, currentColor, #fff 85%);
        --b-time-field-input-background  : transparent;
    }

    /* &.b-open { */
    /*     .b-icon-clock-live { */
    /*         background-color : $main-color !important; */
    /*     } */
    /* } */

    &.b-empty {
        .b-field-trigger {
            animation-delay : -300s;
        }

        &:not(.b-contains-focus) bry-time {
            opacity : 0;
        }
    }

    /* &.b-disabled, */
    /* &.b-read-only { */
    /*     .b-icon-clock-live { */
    /*         background-color : $field-disabled-trigger-color; */
    /*     } */
    /* } */

    /* &:not(.b-disabled, .b-read-only) { */
    /*     .b-icon-angle-left, */
    /*     .b-icon-angle-right { */
    /*         &:hover { */
    /*             color : $main-color; */
    /*         } */
    /*     } */
    /* } */

    &.b-no-steppers {
        .b-step-trigger {
            display : none;
        }
    }

    .b-step-trigger::before {
        /* Override default to not transition transform when flipping the icons */
        transition : color .3s;
    }

    &.b-rtl .b-step-trigger::before {
        transform : scaleX(-1);
    }
}
