/* Not imported by DemoHeader.css on purpose, to not spam CSS bundle */

.demo-header {
    display                    : flex;
    padding-inline             : 0.7em 0;
    padding-block              : 0;
    flex                       : 0 0 auto; /* Must participate in flex using flex-basis: auto */

    background                 : var(--b-demo-header-background);
    border-bottom              : 1px solid var(--b-border-6);
    transition                 : background-color var(--b-default-transition-duration), padding var(--b-default-transition-duration), font-size .1s, border-color var(--b-default-transition-duration);

    a {
        text-decoration : none;
    }

    .b-icon {
        font-size : 1.1em;
    }

    .title {
        margin-inline-end : auto;
        display           : flex;
        align-items       : center;
        white-space       : nowrap;
        color             : var(--b-text-1);

        &:focus-visible {
            outline-offset : var(--b-button-focus-outline-offset);
            outline        : var(--b-button-focus-outline-width) solid var(--b-widget-focus-outline-color);
            border-radius  : var(--b-widget-border-radius);
        }

        h1 {
            display     : flex;
            align-items : center;
            margin      : 0;
            font-weight : 600;
            font-size   : 1.1em;
        }

        svg {
            margin-inline-end  : .75em;
            height             : 2em;
            width              : 2em;
            background         : #0076f8;
            padding-inline-end : .2em;
            border-radius      : 5px;

            g {
                fill : #fff;
            }
        }
    }

    .b-toolbar {
        font-size              : .9em;

        --b-toolbar-padding    : 0.7em;
        --b-toolbar-background : transparent;
    }
}
