:root, :host {
    /**
     * Gap between list items in a multi select list
     */
    --b-list-multi-select-gap              : 0;

    /**
     * Gap within list items
     */
    --b-list-item-gap                      : 0.75em;

    /**
     * List item padding
     */
    --b-list-item-padding                  : 0.75em;

    /**
     * List item padding in a multi select list
     */
    --b-list-multi-select-item-padding     : var(--b-list-item-padding);

    /**
     * List item border radius
     */
    --b-list-item-border-radius            : 0;

    /**
     * Padding for group header items
     */
    --b-list-item-group-padding            : 0.75em 0.75em 0.75em 2em;

    /**
     * Selection icon size (checkmark)
     */
    --b-list-selected-icon-size            : 1.25em;

    /**
     * Selection icon content (checkmark)
     */
    --b-list-selected-icon-content         : var(--b-checkbox-checked-check-content);

    --b-list-checkbox-checked-check-color  : var(--b-text-5);

    /**
     * Group header font-weight
     */
    --b-list-item-group-header-font-weight : calc(var(--b-widget-font-weight) + 200);

    /**
     * List background
     */
    --b-list-background                    : transparent;

    /*
     * List title background, title is sticky and needs a background to cover items scrolling under it.
     * Also applied to the select all item background.
     */
    --b-list-title-background              : var(--b-neutral-100);

    /**
     * List item background
     */
    /*--b-list-item-background                      : null;*/

    /**
     * List item font-weight
     */
    /*--b-list-item-font-weight                     : null;*/

    /**
     * Group header item font-weight
     */
    /*--b-list-item-group-header-font-weight        : null;*/

    /**
     * List item color
     */
    --b-list-item-color                    : var(--b-widget-color);

    /**
     * List title (& select all) font-weight
     */
    --b-list-title-font-weight             : 500;
}

