/* @after Core/widget/Button.css */

:root, :host {
    /**
     * Tab border-radius
     */
    --b-tab-border-radius           : 0;

    /**
     * Indicator for active tab's border-radius
     * @state Active
     */
    --b-tab-indicator-border-radius : 0;

    /**
     * Tab font-weight
     */
    --b-tab-font-weight             : 500;

    /**
     * Tab padding
     */
    --b-tab-padding                 : 0 var(--b-widget-padding-large);

    /**
     * Icon-only tab padding
     */
    --b-tab-icon-only-padding       : 0 var(--b-widget-padding);

    /**
     * Display value for the active tab indicator
     * @state Active
     */
    --b-tab-indicator-display       : block;

    /**
     * Tab background
     */
    --b-tab-background              : transparent;

    /**
     * Tab color
     */
    --b-tab-color                   : var(--b-neutral-40);

    /**
     * Color of close icon for closeable tabs
     */
    --b-tab-close-color             : var(--b-neutral-50);

    /**
     * Hovered close icon color
     * @state Hovered
     */
    --b-tab-close-hover-color       : var(--b-neutral-30);

    /**
     * Active tab's background
     * @state Active
     */
    --b-tab-active-background       : var(--b-tab-background);

    /**
     * Tab border
     */
    --b-tab-border                  : null;

    /**
     * Active tab's border
     * @state Active
     */
    --b-tab-active-border           : null;

    /**
     * Hovered tab's border
     * @state Hovered
     */
    --b-tab-hover-border            : var(--b-tab-border);

    /**
     * Tab label position
     */
    --b-tab-label-position          : null;

    /**
     * Tab indicator height
     */
    --b-tab-indicator-height        : .2em;

    /**
     * Hovered tab's color
     * @state Hovered
     */
    --b-tab-hover-color             : var(--b-neutral-20);

    /**
     * Active tab's color
     * @state Active
     */
    --b-tab-active-tab-color        : var(--b-neutral-0);

    --b-tab-invalid-indicator-color : var(--b-color-red);

    /* Internal vars */
    --bi-tab-indicator-opacity      : 0;
}

/* Colors have to be specified per widget, for color-mix to work as intended with overridden primary color */
