/* @after Core/widget/TextField.css */

:root, :host {
    /**
     * Gap between the start and end date fields
     */
    --b-date-range-field-gap : 0.5em;
}

.b-date-range-field {
    &:not(.b-has-width) {
        width : auto;
    }

    .b-field-container-wrap {
        grid-area : inner;
    }

    .b-field-container {
        --b-container-gap : var(--b-date-range-field-gap);
    }

    .b-field {
        /* Embedded fields have no label, but the field might, which interfers */
        grid-template-areas   : "inner";
        grid-template-columns : 1fr;
    }

    .b-start-date {
        /*flex  : 7 0 13em; /* end-date + 2em (for expand trigger) */
        width : 13em;
    }

    .b-end-date {
        /* flex  : 5 0 11em; */
        width                 : 11em;

        --b-field-trigger-gap : 0;
    }

    .b-end-time,
    .b-start-time {
        flex : 7 7 auto;
    }
}
