:root, :host {
    /**
     * Toast padding
     */
    --b-toast-padding             : var(--b-widget-padding);

    /**
     * Toast border radius
     */
    --b-toast-border-radius       : var(--b-widget-border-radius);

    /**
     * Toast transition duration
     */
    --b-toast-transition-duration : .25s;

    /**
     * Toast progress bar height
     */
    --b-toast-progress-height     : 0.25em;

    /**
     * Toast color
     */
    --b-toast-color               : var(--b-widget-color);
}

.b-internal {
    /* Internal vars */
    --bi-toast-side : null;
}

/* Colors have to be specified per widget, for color-mix to work as intended with overridden primary color */
