:root, :host {
    /**
     * Button box-shadow, overridden by renditions
     */
    --b-button-box-shadow                     : null;

    /**
     * Button text-align
     */
    --b-button-text-align                     : center;

    /**
     * Opacity for disabled buttons
     * @state Disabled
     */
    --b-button-disabled-opacity               : 0.4;

    /**
     * Border-radius for icon-only buttons
     */
    --b-button-icon-only-border-radius        : 50%;

    /**
     * Button text font-weight
     */
    --b-button-font-weight                    : 500;

    /**
     * Gap between icon and label
     */
    --b-button-gap                            : 0.5em;

    /**
     * Button height
     */
    --b-button-height                         : 3em;

    /**
     * Inline padding for buttons with icon
     */
    --b-button-icon-padding-inline            : var(--b-widget-padding);

    /**
     * Inline padding for buttons with icon at the end
     */
    --b-button-end-icon-padding-inline        : var(--b-widget-padding);

    /**
     * Inline padding for buttons with menu
     */
    --b-button-menu-padding-inline            : var(--b-widget-padding);

    /**
     * Button opacity
     */
    --b-button-opacity                        : 1;

    /**
     * Text-only button inline padding
     */
    --b-button-padding-inline                 : var(--b-widget-padding);

    /**
     * Box-shadow for pressed buttons, overridden by renditions
     * @state Pressed
     */
    --b-button-pressed-box-shadow             : none;

    /**
     * Focus outline width
     * @state Focused
     */
    --b-button-focus-outline-width            : var(--b-widget-focus-outline-width);

    /**
     * Focus outline offset
     * @state Focused
     */
    --b-button-focus-outline-offset           : calc(var(--b-widget-focus-outline-width) * -1);

    /**
     * Box-shadow for active buttons, overridden by renditions
     * @state Active
     */
    /*--b-button-active-box-shadow             : null;*/

    /**
     * Button border-radius
     */
    --b-button-border-radius                  : var(--b-widget-border-radius);

    /**
     * Box-shadow for focused buttons, overridden by renditions
     * @state Focused
     */
    /*--b-button-focus-box-shadow              : null;*/

    /**
     * Border-color for focused buttons, overridden by renditions
     * @state Focused
     */
    /*--b-button-focus-border-color            : null;*/

    /**
     * Box-shadow for hovered buttons, overridden by renditions
     * @state Hovered
     */
    /*--b-button-hover-box-shadow              : null;*/

    /**
     * Border-color for disabled buttons
     * @state Disabled
     */
    /*--b-button-disabled-border-color         : null;*/

    /**
     * Box-shadow for active elevated buttons
     * @state Active
     * @rendition Elevated
     */
    --b-button-elevated-active-box-shadow     : var(--b-elevation-1);

    /**
     * Box-shadow for elevated buttons
     * @rendition Elevated
     */
    --b-button-elevated-box-shadow            : var(--b-elevation-1);

    /**
     * Box-shadow for focused elevated buttons
     * @state Focused
     * @rendition Elevated
     */
    --b-button-elevated-focus-box-shadow      : var(--b-elevation-1);

    /**
     * Box-shadow for hovered elevated buttons
     * @state Hovered
     * @rendition Elevated
     */
    --b-button-elevated-hover-box-shadow      : var(--b-elevation-2);

    /**
     * Text color for elevated buttons
     * @rendition Elevated
     */
    /*--b-button-elevated-color          : null;*/

    /**
     * Text color for filled buttons
     * @rendition Filled
     */
    --b-button-filled-color                   : var(--b-text-5);

    /**
     * Box-shadow for hovered filled buttons
     * @state Hovered
     * @rendition Filled
     */
    --b-button-filled-hover-box-shadow        : none;

    /**
     * Box-shadow for hovered tonal buttons
     * @state Hovered
     * @rendition Tonal
     */
    --b-button-tonal-hover-box-shadow         : none;

    /**
     * Border width for tonal buttons
     * @rendition Tonal
     */
    /*--b-button-tonal-border-width           : null;*/

    /**
     * Border width for outlined buttons
     * @rendition Outlined
     */
    --b-button-outlined-border-width          : 1px;

    /**
     * Border color for outlined buttons
     * @rendition Outlined
     */
    --b-button-outlined-border-color          : var(--b-border-6);

    /**
     * Background for disabled outlined buttons
     * @state Disabled
     * @rendition Outlined
     */
    --b-button-outlined-disabled-background   : transparent;

    /**
     * Border color for disabled outlined buttons
     * @state Disabled
     * @rendition Outlined
     */
    --b-button-outlined-disabled-border-color : var(--b-border-6);

    /**
     * Border color for focused outlined buttons
     * @state Focused
     * @rendition Outlined
     */
    /*--b-button-outlined-focus-border-color     : null;*/

    --b-button-outlined-background            : transparent;

    --b-button-outlined-pressed-color         : unset;

    --b-button-type-text-background           : transparent;

    /**
     * Background for disabled text buttons
     * @state Disabled
     * @rendition Text
     */
    --b-button-type-text-disabled-background  : transparent;

    /**
     * Background for disabled buttons
     * @state Disabled
     */
    --b-button-disabled-background            : var(--b-widget-disabled-background);

    /**
     * Disabled button text color
     * @state Disabled
     */
    --b-button-disabled-color                 : var(--b-neutral-50);

    /**
     * Background for focused text buttons
     * @state Focused
     */
    --b-button-type-text-focused-background   : transparent;

    /**
    * Focus outline color
    * @state Focused
    */
    --b-button-focus-outline-color            : var(--b-primary);
}

