    --b-color-pink         : #d81b60;

    /**
     * Default magenta color. Used for example when setting a color prefixed with `b-` for a widget programmatically
     */
    --b-color-magenta      : #c200c2;

    /**
     * Default purple color. Used for example when setting a color prefixed with `b-` for a widget programmatically
     */
    --b-color-purple       : #8e24aa;

    /**
     * Default violet color. Used for example when setting a color prefixed with `b-` for a widget programmatically
     */
    --b-color-violet       : #5e35b1;

    /**
     * Default deep purple color. Used for example when setting a color prefixed with `b-` for a widget programmatically
