/* @after Core/widget/TextField.css */

:root, :host {
    /**
     * Picker color
     */
    --b-text-area-picker-field-picker-color : var(--b-neutral-20);
}

/* Colors have to be specified per widget, for color-mix to work as intended with overridden primary color */
/* .b-bryntum { */
    /**
     * Picker background
     */
    /*--b-text-area-picker-field-picker-background : var(--b-primary-98);*/
/* } */

.b-text-area-picker-field-picker {
    display    : flex;
    min-height : 10em;
    border     : none;
    outline    : none;
    padding    : 1em;

    background : var(--b-text-area-picker-field-picker-background, var(--b-primary-98));
    color      : var(--b-text-area-picker-field-picker-color);

    &:focus {
        border-color : var(--b-primary);
    }
}
