/* Used by DomHelper.highlight */
.b-fx-highlight {
    z-index            : 9999 !important;
    animation          : b-anim-fx-highlight 1s ease 0s 1;
}

@keyframes b-anim-fx-highlight {
    0% {
        box-shadow : none;
    }
    50% {
        box-shadow : 0 0 10px 5px var(--b-primary-50);
    }
    100% {
        box-shadow : none;
    }
}
